@import url('/src/style/homework-17.css');
@import url('/src/style/homework-18.css');

.product {
	display: inline-block;
	background-color: whitesmoke;
	margin: 10px;
	border: 1px solid #eee;
	box-shadow: 0 3px 3px #ccc;
	width: 200px;
	height: 200px;
	padding: 30px;
}
.home {
	text-align: center;
	border: 3px solid green;
	padding: 50px 0;
	margin-top: 100px;
}
@import url('https://fonts.googleapis.com/css?family=Roboto');
/* Global Styles */
:root {
	--nav-primary-color: red;
	--nav-text-color: Black;
	--nav-a-hov-color: Blue;
}
* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
body {
	font-family: 'Roboto', sans-serif;
	font-size: 1rem;
	line-height: 1.6;
	background-color: rgb(126, 123, 123);
	color: #333;
}
a {
	color: var(--nav-primary-color);
	text-decoration: none;
}
a:hover {
	color: #666;
}
ul {
	list-style: none;
}
img {
	width: 100%;
}
/* Utilities */
.container {
	max-width: 1100px;
	margin: auto;
	overflow: hidden;
	padding: 0 2rem;
}
/* Text Styles*/
.x-large {
	font-size: 4rem;
	line-height: 1.2;
	margin-bottom: 1rem;
}
.large {
	font-size: 3rem;
	line-height: 1.2;
	margin-bottom: 1rem;
}
.bg-primary {
	background: var(--nav-primary-color);
	color: var(--nav-text-color);
}
/* Navbar */
.navbar {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 2rem 4rem;
	z-index: 1;
	width: 100%;
	opacity: 0.9;
	margin-bottom: 3rem;
}
.navbar ul {
	display: flex;
}
.navbar a {
	color: var(--nav-text-color);
	padding: 0.45rem;
	margin: 0 0.25rem;
}
.navbar a:hover {
	color: var(--nav-a-hov-color);
}
/* Mobile Styles */
@media (max-width: 700px) {
	.hide-sm {
		display: none;
	}
	.grid-2,
	.grid-3,
	.grid-4 {
		grid-template-columns: 1fr;
	}
	/* Text Styles */
	.x-large {
		font-size: 3rem;
	}
	.large {
		font-size: 2rem;
	}
	.lead {
		font-size: 1rem;
	}
	/* Navbar */
	.navbar {
		display: block;
		text-align: center;
	}
	.navbar ul {
		text-align: center;
		justify-content: center;
	}
}
.gallery {
	display: flex;
	flex-wrap: wrap;
}
.gallery img {
	margin: 5px;
	height: 200px;
	max-width: 300px;
	width: auto;
}
