@import url(https://fonts.googleapis.com/css?family=Roboto);
.Post {
	margin: 5px;
	display: flex;
	flex-direction: row;
	align-content: center;
	width: 100%;
	border: solid black 1px;
	background-color: white;
}
.Post:hover {
	background-color: rgb(223, 219, 219);
}

.post-author-avatar {
	margin: 10px;
	display: flex;
	width: auto;
	height: auto;
}

.post-author-avatar img {
	display: block;
	flex-grow: 0;
	align-self: flex-start;
	width: 48px;
	border-radius: 50%;
	flex-shrink: 1;
	flex-direction: column;
}

.post-info {
	font-weight: 400;
	display: flex;
	flex-direction: row;
}
.post-info h3 {
	margin: 8px;
	font-family: 'Segoe UI', sans-serif;
}

.post-info h4 {
	color: gray;
	margin: 8px;
	font-family: 'Segoe UI', italic;
}

.post-content {
	display: flex;
	flex-direction: column;
}
.post-image {
	width: 98%;
	align-items: center;
}
.post-image img {
	border-radius: 15px;
	max-height: 450px;
	max-width: 98%;
	width: auto;
}
.post-icon {
	display: inline-block;
	width: 350px;
	height: 25px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	width: 100%;
	margin-bottom: 10px;
}
.post-icon-item {
	display: flex;
}

.post-icon-item img {
	margin: 5px;
	display: block;
	width: 20px;
	height: 20px;
}

.Contacts {
	margin: 270px 0px 0px -420px;
	font-family: sans-serif;
	text-align: center;
	display: flex;
	flex-direction: column;
	width: 400px;
	height: 320px;
	background: #37ea37;
}
.wrapper {
	display: flex;
	justify-content: center;
	height: 1250px;
	background: url(/cursor-homework-react/static/media/photo.ce7fb6be.jpg) no-repeat;
}

.contact-list {
	padding-left: 7px;
	display: flex;
	flex-direction: column;
	width: 100%;
	max-height: 320px;
	overflow: auto;
	background: #37ea37;
}
::-webkit-scrollbar {
	width: 7px;
	background-color: #37ea37;
}
.Contact {
	border: solid 1px #abada8;
	padding: 4px;
	margin-top: 5px;
	display: flex;
	flex-direction: row;
}
.Contact p {
	margin: 2px;
}
.contact-info {
	width: 100%;
	font-size: 20px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}
.gender {
	font-size: 20px;
}

.contact-photo {
	font-size: 50px;
}
input {
	width: 100%;
	height: 50px;
	font-size: 20px;
	background-color: #37ea37;
}

.product {
	display: inline-block;
	background-color: whitesmoke;
	margin: 10px;
	border: 1px solid #eee;
	box-shadow: 0 3px 3px #ccc;
	width: 200px;
	height: 200px;
	padding: 30px;
}
.home {
	text-align: center;
	border: 3px solid green;
	padding: 50px 0;
	margin-top: 100px;
}
/* Global Styles */
:root {
	--nav-primary-color: red;
	--nav-text-color: Black;
	--nav-a-hov-color: Blue;
}
* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
body {
	font-family: 'Roboto', sans-serif;
	font-size: 1rem;
	line-height: 1.6;
	background-color: rgb(126, 123, 123);
	color: #333;
}
a {
	color: red;
	color: var(--nav-primary-color);
	text-decoration: none;
}
a:hover {
	color: #666;
}
ul {
	list-style: none;
}
img {
	width: 100%;
}
/* Utilities */
.container {
	max-width: 1100px;
	margin: auto;
	overflow: hidden;
	padding: 0 2rem;
}
/* Text Styles*/
.x-large {
	font-size: 4rem;
	line-height: 1.2;
	margin-bottom: 1rem;
}
.large {
	font-size: 3rem;
	line-height: 1.2;
	margin-bottom: 1rem;
}
.bg-primary {
	background: red;
	background: var(--nav-primary-color);
	color: Black;
	color: var(--nav-text-color);
}
/* Navbar */
.navbar {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 2rem 4rem;
	z-index: 1;
	width: 100%;
	opacity: 0.9;
	margin-bottom: 3rem;
}
.navbar ul {
	display: flex;
}
.navbar a {
	color: Black;
	color: var(--nav-text-color);
	padding: 0.45rem;
	margin: 0 0.25rem;
}
.navbar a:hover {
	color: Blue;
	color: var(--nav-a-hov-color);
}
/* Mobile Styles */
@media (max-width: 700px) {
	.hide-sm {
		display: none;
	}
	.grid-2,
	.grid-3,
	.grid-4 {
		grid-template-columns: 1fr;
	}
	/* Text Styles */
	.x-large {
		font-size: 3rem;
	}
	.large {
		font-size: 2rem;
	}
	.lead {
		font-size: 1rem;
	}
	/* Navbar */
	.navbar {
		display: block;
		text-align: center;
	}
	.navbar ul {
		text-align: center;
		justify-content: center;
	}
}
.gallery {
	display: flex;
	flex-wrap: wrap;
}
.gallery img {
	margin: 5px;
	height: 200px;
	max-width: 300px;
	width: auto;
}

